import { useTranslation } from "react-i18next";
import {
  assestsFeedbackUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import style from "../InstituteFeedback.module.css";
import { Link, useLocation, useNavigate, useParams } from "react-router-dom";
import { Suspense, lazy, useState } from "react";
import dayjs from "dayjs";
import { useFeedbackAnalyticProcess } from "../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { localTimeCompare } from "../../../Utils/Functions/localTime";
const StudentFeedbackCardMenu = lazy(() => import("./StudentFeedbackCardMenu"));
const GivenStudentFeedbackList = lazy(() =>
  import("./StudentFeedbackDetail/GivenStudentFeedbackList")
);
const StudentFeedbackCard = ({
  feedback,
  onRefetch,
  instituteId,
  isManager,
}) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [disabled, setDisabled] = useState(false);
  const [menu, setMenu] = useState(false);
  const [open, setOpen] = useState(false);

  const [feedbackAnalyticProcess] = useFeedbackAnalyticProcess();
  const onToggle = (e) => {
    if (e) e.preventDefault();
    setMenu((pre) => !pre);
  };
  const onOpenToggle = (e) => {
    if (e) e.preventDefault();
    setOpen((pre) => !pre);
  };

  const onAnalytic = (e) => {
    e.preventDefault();
    if (isManager) {
      navigate(
        `/q/${params.username}/member/staff/feedback?a=feedback-analytic`,
        {
          state: {
            ...getQuery.state,
            ifid: feedback?._id,
          },
        }
      );
    } else {
      navigate(
        `/q/${params.username}/institute/member/feedback?a=feedback-analytic`,
        {
          state: {
            ...getQuery.state,
            ifid: feedback?._id,
          },
        }
      );
    }
  };

  const onEvluationAnalytic = (e) => {
    e.preventDefault();
    if (feedback?._id) {
      setDisabled((pre) => !pre);
      feedbackAnalyticProcess({
        ifid: feedback?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <Link
        to={
          isManager
            ? `/q/${params.username}/member/staff/feedback?a=feedback-detail`
            : `/q/${params.username}/institute/member/feedback?a=feedback-detail`
        }
        state={{
          ...getQuery.state,
          ifid: feedback?._id,
          feedbackName: feedback?.feedback_name,
        }}
      >
        <div className={style.sf_card}>
          <div className={style.sf_card_contatiner}>
            <img
              src={`${assestsFeedbackUrl}/feedback.svg`}
              loading="lazy"
              alt="avatar"
            />

            <div className={style.sf_card_contatiner_inner}>
              <h6>{feedback?.feedback_name ?? ""}</h6>
              <p>
                {t("question")} {": "} {feedback?.question_count ?? 0}
              </p>
              <p>
                {t("date")} {": "}{" "}
                {dayjs(feedback?.created_at)?.format("DD/MM/YYYY")}
                {feedback?.feedback_close_at ? (
                  <>
                    {","}
                    {t("to")}{" "}
                    {dayjs(feedback?.feedback_close_at)?.format("DD/MM/YYYY")}
                  </>
                ) : null}
              </p>
              {feedback?.department?.length > 0 ? (
                <p>
                  {t("department")} {": "}{" "}
                  {feedback?.department?.map((gt, index) => (
                    <span key={`${gt?._id}${index}`}>
                      {index !== 0 ? "," : ""}
                      {gt?.dName ?? ""}
                    </span>
                  ))}
                </p>
              ) : null}

              {feedback?.evaluation ? (
                <p>
                  {t("total_feedback_given")} {": "}{" "}
                  <span
                    style={{
                      fontWeight: "500",
                      color: "#00f",
                      fontSize: "16px",
                      cursor: "pointer",
                    }}
                    onClick={onOpenToggle}
                  >
                    {feedback?.feedback_given_student_count}
                  </span>
                  /{feedback?.feedback_notify_count}
                </p>
              ) : null}
            </div>
          </div>
          <div className={style.sf_card_contatiner}>
            {feedback?.analytic_evaluation ? (
              <CreateButton
                label={t("view_analytic")}
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 1rem",
                  height: "fit-content",
                }}
                onAction={onAnalytic}
              />
            ) : (
              feedback?.feedback_close_at &&
              localTimeCompare(
                "isAfter",
                "day",
                feedback?.feedback_close_at
              ) && (
                <CreateButton
                  label={t("analytic")}
                  customStyle={{
                    margin: "0",
                    padding: "0.4rem 1rem",
                    height: "fit-content",
                  }}
                  onAction={onEvluationAnalytic}
                />
              )
            )}

            {feedback?.evaluation ? null : (
              <img
                src={`${assestsUserFinanceUrl}/menu.svg`}
                alt="menu icon"
                onClick={onToggle}
                title="Menu"
                style={{
                  cursor: "pointer",
                  height: "1.5rem",
                }}
              />
            )}
          </div>
        </div>
      </Link>
      <Suspense fallback={<QvipleLoader />}>
        {menu && (
          <StudentFeedbackCardMenu
            onClose={onToggle}
            feedback={feedback}
            onRefetch={onRefetch}
            instituteId={instituteId}
          />
        )}
        {open && (
          <GivenStudentFeedbackList
            onClose={onOpenToggle}
            ifid={feedback?._id}
          />
        )}
      </Suspense>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StudentFeedbackCard;
